/* Root container */
.admin-container {
  padding: 2rem;
  max-width: 1200px;
  margin: auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

/* Titles */
.admin-title {
  font-size: 2rem;
  margin-bottom: 1.2rem;
  text-align: center;
  color: #e67e22;
}

/* Section titles */
.section-title {
  margin-top: 2rem;
  font-size: 1.4rem;
  border-left: 4px solid #e67e22;
  padding-left: 8px;
}

/* Tab buttons */
.tab-buttons {
  text-align: center;
  margin-bottom: 1.2rem;
}

.tab-buttons button {
  background: #f5f5f5;
  border: none;
  margin: 0 0.4rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 6px;
  font-weight: 500;
  transition: background 0.2s;
}

.tab-buttons button:hover {
  background: #ddd;
}

.tab-buttons button.active {
  background: #e67e22;
  color: white;
}

/* Card base */
.card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  padding: 1rem;
  transition: transform 0.2s;
}

.card:hover {
  transform: translateY(-4px);
}

/* Forms */
.add-item-card input,
.add-item-card select,
.add-event-form input,
.add-event-form select {
  margin: 0.3rem 0;
  padding: 0.5rem;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 0.95rem;
}

.add-event-form {
  display: grid;
  gap: 0.6rem;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  margin-bottom: 1rem;
}

/* Buttons */
.btn-primary {
  background: #e67e22;
  color: white;
  padding: 0.5rem 0.9rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-primary:hover {
  background: #cf711f;
}

.btn-secondary,
.btn-cancel,
.btn-delete {
  margin: 0.3rem;
  padding: 0.4rem 0.7rem;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.btn-secondary { background: #3498db; color: white; }
.btn-secondary:hover { background: #2980b9; }

.btn-cancel { background: #bdc3c7; }
.btn-cancel:hover { background: #95a5a6; }

.btn-delete { background: #e74c3c; color: white; }
.btn-delete:hover { background: #c0392b; }

/* File drop */
.file-drop {
  border: 2px dashed #ccc;
  border-radius: 8px;
  text-align: center;
  padding: 1rem;
  cursor: pointer;
  color: #888;
  margin: 0.6rem 0;
  transition: border-color 0.2s, background 0.2s;
}

.file-drop:hover {
  border-color: #e67e22;
  background: #fafafa;
}

.file-drop img {
  max-width: 100px;
  border-radius: 8px;
  margin-top: 0.5rem;
}

/* Filter & search */
.filter-search {
  display: flex;
  gap: 0.6rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.filter-search select,
.filter-search input {
  padding: 0.4rem;
  border: 1px solid #ccc;
  border-radius: 6px;
}

/* Grids */
.menu-items-grid, .events-list {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

.menu-item-card img, .event-card img {
  width: 100%;
  height: 140px;
  object-fit: cover;
  border-radius: 8px;
}

/* Info */
.item-info {
  margin-top: 0.5rem;
}

.price {
  color: #e67e22;
  font-weight: bold;
}

.badge {
  display: inline-block;
  background: #e67e22;
  color: white;
  border-radius: 12px;
  padding: 0.2rem 0.6rem;
  font-size: 0.8rem;
  margin-top: 0.3rem;
}

/* Edit buttons */
.edit-buttons {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.5rem;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1rem;
}

.pagination button {
  background: #e67e22;
  color: white;
  border: none;
  padding: 0.4rem 0.8rem;
  border-radius: 4px;
  cursor: pointer;
}

.pagination button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

/* Event card text */
.event-card h4 {
  margin: 0.3rem 0;
}

.event-card p {
  font-size: 0.9rem;
  color: #555;
}

/* Transition on hover */
.menu-item-card:hover, .event-card:hover {
  transform: translateY(-4px);
}
