/* splash-container */
.splash-container-new {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 9999;
}


/* blurred circle behind logo */
.blurred-circle {
  position: absolute;
  width: 50rem;
  height: 50rem;
  background: radial-gradient(circle, rgba(217, 137, 33, 0.185) 0%, rgba(165, 107, 30, 0.711) 60%);
  filter: blur(60px);
  border-radius: 50%;
  z-index: 1;
}

/* logo animation float */
.logo {
  position: relative;
  z-index: 2;
}

/* brand title with gradient & glow */
.brand {
  font-size: 3rem;
  font-weight: bold;
  font-family: 'Playfair Display', serif;
  text-align: center;
  color: transparent;
  background: linear-gradient(90deg, #d4af7f, #fff4d0, #d4af7f);
  background-size: 200% auto;
  background-clip: text;
  -webkit-background-clip: text;
  animation: shimmer 2s linear infinite, pulse 2.5s ease-in-out infinite;
  margin: 0.5rem 0;
  z-index: 2;
}

/* subtle tagline */
.tagline {
  font-size: 1.1rem;
  font-family: 'Playfair Display', serif;
  color: #d4af7f;
  margin-bottom: 1.2rem;
  text-align: center;
  opacity: 0.85;
  z-index: 2;
}
.animate-fade-in {
  opacity: 0;
  animation: fadeGlow 2s ease forwards;
  animation-delay: 1s;
}

@keyframes fadeGlow {
  0% {
    opacity: 0;
    letter-spacing: 0.1em;
    text-shadow: none;
  }
  50% {
    opacity: 1;
    letter-spacing: 0.15em;
    text-shadow: 0 0 5px rgba(255, 215, 160, 0.5);
  }
  100% {
    opacity: 1;
    letter-spacing: 0.1em;
    text-shadow: none;
  }
}
.shimmer-tagline {
  position: relative;
  display: inline-block;
  overflow: hidden;
}

.shimmer-tagline::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  background: linear-gradient(60deg, transparent, rgba(255,255,255,0.4), transparent);
}


/* enter button */
.enter-button {
  padding: 0.6rem 1.5rem;
  font-size: 1rem;
  font-weight: 500;
  color: #1b1b1b;
  background: #d4af7f;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  z-index: 2;
}

.enter-button:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px rgba(212,175,127,0.6);
}

/* scroll hint */
.scroll-hint {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: #888;
  z-index: 2;
  animation: fadeInUp 2s ease-in-out;
}

/* particles bg always behind */
.particles-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
}

/* fade out splash */
.fade-out {
  opacity: 0;
  transition: opacity 0.6s ease;
}

/* animations */
@keyframes shimmer {
  0% { background-position: -200% center; }
  100% { background-position: 200% center; }
}

@keyframes pulse {
  0%, 100% { text-shadow: 0 0 10px rgba(212,175,127,0.7); }
  50% { text-shadow: 0 0 20px rgba(212,175,127,1); }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes fadeInUp {
  0% { opacity: 0; transform: translateY(10px); }
  100% { opacity: 1; transform: translateY(0); }
}
